<template>
    <div>
        <Address-Header :title="id==null?'新增地址':'修改地址'"></Address-Header>
        <div class="address">
            <div class="address-box">
                <label for="">收货人：</label>
                <input type="text" v-model="name" placeholder="收货人姓名">
            </div>

             <div class="address-box">
                <label for="">手机号码：</label>
                <input type="text" v-model="phone" placeholder="收货人的手机号码">
            </div>

             <div class="address-box">
                <label for="">选择地区：</label>
                <input type="text" v-model="zone" placeholder="请选择收货地址" @click="select" readonly>

            </div>

             <div class="address-box">
                <label for="">详细地址：</label>
                <input type="text" v-model="detail" placeholder="详细街道地址">
            </div>
            <div class="address-box">
              <label for="">默认地址：</label>

              <van-switch v-model="isDefault" size="20px"/>
            </div>
        </div>

        <div class="footer">
            <a @click="save">保存地址</a>
        </div>

      <div style="font-size: 0.4rem">
      <van-popup v-model="showArea" position="bottom" :overlay="true">
        <van-area :area-list="areaList" @confirm="confirm" @cancel="cancel" :value="currAddress"/>
      </van-popup>
      </div>
    </div>
</template>

<script>
import AddressHeader from "../common/header";
import { mapGetters, mapMutations } from "vuex";
import { Toast } from "mint-ui";
import area from "@/utils/area.js"
import api from "@/api"

export default {
  name: "add_address",
  data() {
    return {
      id: null,
      name: "",
      phone: "",
      zone: "",
      provinceId:0,
      cityId: 0,
      districtId: 0,
      detail: "",
      areaList: area,
      showArea: false,
      isDefault: false,
      currAddress: 0,
    };
  },
  components: {
    AddressHeader
  },
  mounted:function(){

    var _this = this;
    this.id = this.$route.query.id;

    if (this.id != undefined){
      api.getAddress(this.id).then(function({data}){

        if (data.errno == 0){
          _this.name = data.data.name;
          _this.phone = data.data.mobile;
          _this.provinceId = data.data.provinceId;
          _this.cityId = data.data.cityId;
          _this.districtId = data.data.districtId;
          _this.isDefault = data.data.isDefault;
          _this.detail = data.data.address;
          _this.zone = area.province_list[_this.provinceId]+"-"
            +area.city_list[_this.cityId]+"-"
            +area.county_list[_this.districtId];

          _this.currAddress = _this.districtId.toString();

        }
      })
    }


  },
  methods: {
    save() {
      var _this = this;
      if (
        this.name == "" ||
        this.phone == "" ||
        this.zone == "" ||
        this.detail == ""
      ) {
        Toast({
          message: "信息请填写完整",
          duration: 950
        });
        retrun;
      }

      if (!this.validate.validatePhone(this.phone)){
        Toast("手机格式不正确");
        return;
      }

      var data = {
        id: this.id,
        name: this.name,
        mobile: this.phone,
        //zone: this.zone,
        provinceId: this.provinceId,
        cityId: this.cityId,
        areaId: this.districtId,
        address: this.detail,
        isDefault: this.isDefault,
      };

      api.saveAddress(data).then(function({data}){

        if (data.errno == 0){

          _this.$router.back();
        }
        else{
          Toast(data.errmsg)
        }
      })

    },
    select(){
      this.showArea = true;
    },
    confirm(data){
      console.log(data)
      this.zone = data[0].name+"-"+data[1].name+"-"+data[2].name;
      this.provinceId = data[0].code;
      this.cityId = data[1].code;
      this.districtId = data[2].code;
      this.showArea = false;
    },
    cancel(){
      this.showArea = false;
    }
  }
};
</script>

<style lang="stylus" scoped>
.address {
    padding-top: 1.45rem;

    .address-box {
        width: 100%;
        height: 1.5rem;
        line-height: 1.5rem;
        background: #fff;
        float: left;

        label {
            width: 30%;
            height: 100%;
            padding-left: 0.58rem;
            font-size: 0.35rem;
            display: block;
            float: left;
        }

        input {
            width: 70%;
            font-size: 0.35rem;
        }
    }

}
.van-switch--on {
  background-color: #2dacff;
}

.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 1.3rem;
    border-top: 1px solid #e0e0e0;
    background: #ffffff;

    a {
        float: none;
        display: block;
        margin: 0.18rem auto;
        text-align: center;
        width: 95%;
        height: 0.9rem;
        line-height: 0.9rem;
        border-radius: 20px;
        font-size: 0.28rem;
        color: #fff;
        background-color: #00acff;
    }
}
</style>



