<template>
<div class="goodDetail" id="transitionName">
    <Detail-Header title="商品详情"></Detail-Header>
      <Loading v-if="isLoading"></Loading>
      <div class="goodDetailList" v-else>
            <ul style="background: white;">
                <li>
                    <div class="goodDetaiSwipe">
                        <mt-swipe :auto="4000">
                            <mt-swipe-item v-for="url in goods.info.gallery">
                                <img :src="url" alt="图片">
                            </mt-swipe-item>
                        </mt-swipe>
                    </div>
                  <div class="goodDetailMain">
                    <div class="gooDetailNumber">商品编号：{{goods.info.goodsSn}}</div>
                    <div class="goodDetailName">{{goods.info.name}}</div>
                    <div style="text-align: justify;font-size: 0.348rem;">
                      <span style="margin-left:-.2rem;color:#FF4B3D;" v-if="goods.info.keywords">【{{goods.info.keywords}}】</span>
                      {{goods.info.goodsBrief}}
                    </div>
                    <div class="goodDetailColor"></div>
                    <div class="goodDetailPaid">￥{{goods.info.retailPrice}}</div>
                  </div>



                    <div class="goodDetailValue">

                      <div class="_Value">购买数量：</div>
                      <div class="_cartNumber" style="margin-left: 2rem;">
                        <a href="javascript:;" @click="jian(index)" class="goodDetailReduce">-</a>
                        <input type="text"   v-model="buyNumber" readonly="readonly" style="font-size: 0.4rem"/>
                        <a href="javascript:;" @click="jia(index)" class="goodDetailAdd">+</a>
                      </div>
                    </div>

                 <!-- <div class="goodDetailValue">

                    <div class="_Value">支付方式：</div>
                    <div class="van-sku-row__items" style="padding-left: 0.3rem">
                      <span style="margin-left: 0.2rem" class="van-sku-row__item" v-for="payment,index in payments"
                            v-bind:class="{'van-sku-row__item&#45;&#45;active':selectPayment==index}" @click="changePayment(index)">
                        {{payment.value}}
                      </span></div>
                  </div>-->

                   <!-- <Detail-Layer></Detail-Layer>-->

                    <div class="goodDetailBox">
                        <mt-navbar v-model="selected" >
                            <mt-tab-item id="tab-container1">图文详情</mt-tab-item>
                            <mt-tab-item id="tab-container2">参数</mt-tab-item>
                        </mt-navbar>


                        <mt-tab-container v-model="selected" swipeable>
                            <mt-tab-container-item id="tab-container1">
                               <div class="goodDetailImg" v-html="goods.info.goodsDesc">
                                 <!--  <p v-for="Image in goodDetail.Images">
                                       <img v-bind:src="Image.one" alt="详情图片">
                                    </p>-->
                                </div>
                            </mt-tab-container-item>

                            <mt-tab-container-item id="tab-container2">
                                <!--<div class="peizhi" v-html="goodDetail.homePeizhi"></div>-->
                              <div class="peizhi table-container">
                                <table class="table">
                                  <thead>
                                    <tr>
                                      <th>属性</th>
                                      <th>值</th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    <tr v-for="attr in goods.attribute">
                                      <td>{{attr.attribute}}</td>
                                      <td>{{attr.value}}</td>
                                    </tr>
                                  </tbody>
                                </table>
                                <p></p>
                              </div>
                            </mt-tab-container-item>
                        </mt-tab-container>

                    </div>


                    <div class="goodDetailFooter">
                        <div class="left">
                            <div class="cart">
                                <div class="cartlength">{{cartlength}}</div>
                               <img src="/static/img/cart.png" >
                                <span>购物车</span>
                            </div>
                            <div class="collection" >
                                <div class="collection-box" @click="addCollection(1)"  v-if="goods.userHasCollect==0">
                                    <i class="iconfont icon-collection"></i>
                                    <span>收藏</span>
                                </div>
                                <div class="collection-box" @click="addCollection(0)"  v-else>
                                    <i class="iconfont icon-shoucangxuanzhong1" style="color:#f99800"></i>
                                    <span style="color:#f99800">已收藏</span>
                                </div>
                            </div>
                            <!--<div class="shop">
                                <img src="http://p6563v2ck.bkt.clouddn.com/%E5%BA%97%E9%93%BA_2.png" >
                                &lt;!&ndash; <i class="iconfont icon-xuanzekuangxuanzhong" v-show="!$store.state.collection"></i>
                                <i class="iconfont icon-xuanzekuangxuanzhong" v-show="$store.state.collection" style="color:red"></i> &ndash;&gt;
                                <span>店铺</span>
                            </div>-->
                        </div>
                        <div class="rigth">
                            <div class="add">
                                <a href="javascript:void(0);" @click="onAddCartClicked">加入购物车</a>
                            </div>
                            <div class="purchase">
                                <a href="javascript:void(0);" @click="onBuyClicked">提交订单</a>
                            </div>
                        </div>

                    </div>

                </li>
            </ul>
      </div>
  </div>
</template>

<script >
import { MessageBox,Navbar,TabItem,TabContainer,TabContainerItem } from "mint-ui";
import { Toast } from 'vant';
import { mapGetters, mapMutations } from "vuex";
import DetailHeader from "./component/DetailHeader";
import DetailLayer from "./component/DetailLayer";
import axios from "axios";
import request from '@/utils/request'
import Header from "../../common/header";
import weixi from '@/utils/weixi.js'
import api from '@/api'

export default {
  name: "goodDetail",
  data() {
    return {
      active: "1",
      goodDetailHeader: "商品详情",
      selected: "tab-container1",
      productList: [],
      goods: {},
      cartlength: 0,
      goodDetail: {
        id : 0,
      },
      isLoading: true,
      userInfo : null, //weixi.getUserInfo(),
      payments:[],  //支付方式
      selectPayment: 0 , //当前支付方式
      buyNumber: 1 , //购买数量
    };
  },
  components: {
    Header,
    DetailHeader,
    DetailLayer
  },
  computed: {

  },
  mounted() {

  },
  created() {
    var _this = this;
    _this.goodDetail.id = this.$route.query.id;
    /*axios.get("/static/ceshi.json").then(res => {
      for (var i = 0; i < res.data.data.home.length;i++){
        if (res.data.data.home[i].id == 1 ) {
            _this.goodDetails.push(res.data.data.home[i]);
        }
      }
    });*/

    request({
      url: 'wx/goods/detail',
      method: 'get',
      params: {id:_this.goodDetail.id},
    }).then(function({data}) {
      //console.log(data)

      if (data.errno != 0){
        _this.$router.go(-1);
        return;
      }

      _this.isLoading = false
      _this.goods = data.data;

      _this.payments = _this.goods.specificationList[0].valueList;
      _this.productList = _this.goods.productList;



      /*_this.payments = _this.payments.map(function(item){

        item.goodsPrice = '';
        productList.forEach(function (elem, index, array){
          if (elem.goodsSpecificationIds.indexOf(item.id)!= -1){

            if (elem.retailPrice >0 ){
              item.goodsPrice = '￥'+elem.retailPrice;
            }
            if (elem.pointPrice > 0){
              if (item.goodsPrice.length > 0){
                item.goodsPrice += ' + '
              }
              item.goodsPrice += '积分:'+elem.pointPrice;
            }

            item.productId = elem.id;
            item.product = elem;
          }
        })

        return item;
      })*/

      console.log(_this.payments)
console.log(_this.goods)

    });

    //取购物车列表
    if (weixi.getToken() != undefined) {
      api.cartsList().then(function ({data}) {
        // console.log(data)
        if (data.errno == 0) {

          _this.cartlength = data.data.cartTotal.goodsCount;
        }
      })
    }

  },

  methods: {
    //收藏
    addCollection(index) {

      if (this.userInfo == null){
        this.userInfo = weixi.getUserInfo();
      }
      this.goods.userHasCollect = index;

      var data = {
        typeId:0,
        valueId:this.goodDetail.id
      }
      api.addCollection(data).then(function({data}) {
        console.log(data)
        if (data.errno == 0){
          if (data.data.type == "add"){
            Toast.success("收藏成功!")
          }
          else{
            Toast.success("取消收藏成功!")
          }

        }
      });

    },
    // 点击按钮时，首先判断该商品是否在购物车已存在，如果存在则不再加入
    onAddCartClicked: async function() {
      var _this = this;

      Toast.loading({
        mask: true,
        message: '处理中...'
      });

      var isValidate = await this.validate();

      if (!isValidate){
        return false;
      }

        //{ goodsId: xxx, productId: xxx, number: xxx }
        var data = {
          goodsId: this.goods.info.id,
          productId: this.productList[this.selectPayment].id,
          number: this.buyNumber
        }
        api.addCarts(data).then(function ({data}) {
          if (data.errno == 0){
            _this.cartlength = data.data;
            Toast.clear();
            Toast.success("加入购物车成功！");
          }
          else{
            Toast.clear();
            Toast(data.errmsg);
          }
        })
    },

    //返回上一级
    fanhui: function() {
      this.$router.go(-1);
    },
    //购买
    onBuyClicked: async function() {

      Toast.loading({
        mask: true,
        message: '处理中...'
      });
      var isValidate = await this.validate();

      if (!isValidate){
        return false;
      }
      console.log(this.productList);

      //保存到vuex
      this.$store.commit('addPayGoods',[{
        goodsId:this.goods.info.id,
        goodsName:this.goods.info.name,
        productId:this.productList[this.selectPayment].id,
        productImg:this.goods.info.primaryPicUrl,
        price: this.productList[this.selectPayment].retailPrice,
        //point: this.payments[this.selectPayment].product.pointPrice,
        quantity:this.buyNumber}])

        Toast.clear();
        this.$router.push({
            path:"pay"
        })

    },
    jia: function(index) {
      this.buyNumber++;

    },
    jian: function(index) {
      if (this.buyNumber == 1) {
        this.buyNumber = 1;
      } else {
        this.buyNumber--;
      }
    },
    changePayment:function(index){
      this.selectPayment = index;
    },
    validate: async function(){
      var _this = this;
      var returnVal = await weixi.getUserInfo();

      if (returnVal == null){
        return false;
      }

     /* if (returnVal.data.point < _this.payments[_this.selectPayment].product.pointPrice){
        Toast("积分不足!")
        return false;
      }*/

      //检查库存
      /*if (_this.buyNumber > _this.payments[_this.selectPayment].product.goodsNumber){
        Toast("库存不足!")
        return false;
      }*/

      return true;
    },

  }
};
</script>

