}
  .goodDetailStock {
    font-size :0.35rem;
    padding: 5px 10px 0px 0;
    color:#666;
  }

.goodDetailFooter {
    position: fixed;
    width: 100%;
    bottom: 0rem;
    height: 1.2rem;
    background: #F6F4F7;
    border-top: 1px solid #efefef;

    .left {
        width: 30%;
        float: left;
        position: relative;
        font-size: .35rem;
        .cart {
            width: 50%;
            display: flex;
            justify-content: center;
