<template>
  <div class="o1">
      <Order-Header title="订单详情"></Order-Header>
        <div class="details-box">
      <div class="details-success">
          <p>订单状态：{{orderInfo.orderStatusText}}</p>
          <img src="https://shopstatic.vivo.com.cn/vivoshop/wap/dist/images/membercenter/order/no-pay_79c2dfe.png">
      </div>

        <div class="details-address" v-show="orderInfo.delivery == '快递'">
        <p class="address-box">
            <span class="name">收货人：{{orderInfo.consignee}}</span>
            <span class="phone">{{orderInfo.mobile}}</span>
        </p>
        <p class="address-details">收货地址：{{orderInfo.address}}</p>
      </div>
      <div class="details-list" >
        <div class="details-list-1">
          <div v-for="(list,index) in orderGoods" :key="index">
            <img :src="list.picUrl">
            <p>
              <!-- <span>id是{{list.id}}</span> -->
              <span class="name">{{list.goodsName}}
            <p>× {{list.number}}</p></span>
            <span class="price">¥ {{list.retailPrice}}</span>
            </p>
            <div style="clear: both"></div>
          </div>

        </div>
       <div class="details-list-2">


           <div class="details-list-2-2">
                <p>
                    <span class="span-1">商品总金额：</span>
                    <span class="span-2">¥ {{orderInfo.goodsPrice|toFixed}}</span>
                </p>
                 <p>
                   <span class="span-1">积分抵扣：</span>
                   <span class="span-2">¥ {{orderInfo.integralPrice|toFixed}}</span>
                 </p>
                 <p>
                     <span class="span-1">运费：</span>
                     <span class="span-2">¥ {{orderInfo.freightPrice|toFixed}}</span>
                </p>
                <p>
                     <span class="span-1">优惠：</span>
                     <span class="span-2 red">-¥ 0.00</span>
                </p>
             <p>
               <span class="span-1">配送方式：</span>
               <span class="span-2">【{{orderInfo.delivery}}】 {{deliveryText}}</span>
             </p>

           </div>

            <!--<div class="details-list-2-3">
               &lt;!&ndash; <p>
                    <span class="span-1">配送方式：</span>
                    <span class="span-2">{{orderInfo.shipChannel}}  {{orderInfo.shipSn}}</span>
                </p>&ndash;&gt;
                &lt;!&ndash; <p>
                     <span class="span-1">支付方式：</span>
                     <span class="span-2"></span>
                </p>&ndash;&gt;

           </div>-->

         <div class="details-list-2-1">
           <!--<p>
               <span class="span-1">订单优惠：</span>
               <span class="span-2">订单满68元包邮</span>
           </p>-->
           <p>
             <span class="span-1">订单备注：</span>
             <span class="span-2">{{orderInfo.remarks}}</span>
           </p>
         </div>
       </div>
        <!-- <img :src="list.homeImg" /> -->

        <div class="order-footer">
          <div class="left">
            实付金额：
            <span>¥{{orderInfo.actualPrice|toFixed}}</span>
          </div>

          <div class="right" v-show="orderInfo.handleOption.pay" @click="onPay">
            立即支付
          </div>
          <div class="right" v-show="orderInfo.handleOption.confirm" @click="onComfirm">
            确认收货
          </div>
          <div class="right cancel" v-show="orderInfo.handleOption.cancel" @click="onCancel">
            取消订单
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import OrderHeader from "../../common/header";
import api from '@/api'
import {MessageBox} from 'mint-ui'
import {Toast} from 'vant'
export default {
  name: "o1",
  data() {
    return {
      orderId: this.$route.query.id,
      orderInfo:{},
      orderGoods:[],
      deliveryText: '',
    };
  },
  components: {
    OrderHeader
  },
  created() {
    this.loadorderDetail();
  },
  methods:{
    loadorderDetail(){
      var _this = this;
      api.orderDetail({orderId:this.orderId}).then(function ({data}) {

        if (data.errno == 0){
          _this.orderGoods = data.data.orderGoods;
          _this.orderInfo = data.data.orderInfo;
          if (_this.orderInfo.shipSn != null){
            _this.deliveryText = _this.orderInfo.shipChannel+"："+_this.orderInfo.shipSn;
          }
        }
        //console.log(data)
      })
    },
    /**
     * 确认订单
     */
    onComfirm(){
      var params = {orderId: this.orderInfo.id}
      var _this = this;
      MessageBox.confirm("确认货品已经收到?").then(action =>{
        if (action == "confirm"){
          Toast.loading();
          api.confirmOrder(params).then(({data})=>{
            if(data.errno ==0){

              _this.loadorderDetail();
            }
            Toast.clear();
          })
        }
      });
    },
    onPay(){
      this.$router.push({
        path:'/weixiPay',
        query: { orderId: this.orderId,amount: this.orderInfo.actualPrice}
      })
    },
    onCancel(){

      var _this = this;
      MessageBox.confirm("确认取消这笔订单?").then(action=>{
        if (action == 'confirm'){
          Toast.loading();
          var data = {orderId: _this.orderInfo.id}
          api.cancelOrder(data).then(function ({data}) {
            if (data.errno == 0){
              _this.loadorderDetail();
              Toast.clear();
            }
            else{
              Toast.clear();
            }

          })
        }
      })
    }
  }
};
</script>

<style lang="stylus" scoped>
.details-box {
    position: relative;
    top: 1.45rem;
}

.details-success {
    width: 100%;
    height: 2.4rem;
    background: #69c1ff;

    p {
        color: #fff;
        font-size: 0.46rem;
        line-height: 2.4rem;
        float: left;
        padding-left: 0.4rem;
    }

    img {
        width: 2.7rem;
        margin: 0.5rem;
        float: right;
    }
}

.details-address {
    width: 100%;
    /*height: 2.7rem;*/
    padding-bottom:0.2rem;
    background: #fff;

    .address-box {
        width: 87%;
        margin: auto;
        font-size: 0.4rem;
        padding-top: 0.3rem;
        padding-bottom: 0.3rem;

        .phone {
            float: right;
        }
    }

    .address-details {
        width: 87%;
        margin: auto;
        color: #666;
        font-size: 0.38rem;
    }
}

.details-list {
    width: 100%;
    margin-bottom: 1.3rem;

    .details-list-1 {
        width: 100%;
        /*height: 3rem;*/
        background: #ffffff;
        border-top:1px solid #e3e3e3;
        /*margin-top: 1px;*/

        img {
            width: 2.5rem;
            margin: 0.28rem;
            float: left;
        }

        p {
            display: flex;
            flex-direction: column;

            .name {
                font-size: 0.4rem;
                margin-top: 0.3rem;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                /*height: 0.6rem;*/
            }

            .price {
                color: red;
                font-size: 0.35rem;
                font-weight: 500;
                height: 0.6rem;
            }

            span {
                p {
                    float: right;
                    margin-right: 0.5rem;
                }
            }
        }
    }

    .details-list-2 {
        width: 100%;
        /*height: 8rem;*/
        background: #ffffff;

        div {
            width: 90%;
            margin: auto;
            padding-top: 0.3rem;
            padding-bottom: 0.3rem;
            font-size: 0.36rem;
            border-top: 1px solid #e8e8e8;

            span {
                color: #888;
                float: left;
            }

            .span-1 {
                width: 2.3rem;
                display: block;
            }
        }

        .details-list-2-1 {
            height: 2rem;

            p {
                height: 0.76rem;
                line-height: 0.76rem;
            }
        }

        .details-list-2-2 {
            /*height: 2.8rem;*/

            p {
                height: 0.76rem;
                line-height: 0.76rem;
            }

            .red {
                color: red;
            }
        }

        .details-list-2-3 {
            height: 4rem;

            p {
                height: 0.76rem;
                line-height: 0.76rem;
            }
        }
    }

  .order-footer {
    width: 100%;
    height: 1.3rem;
    background: #fff;
    position: fixed;
    bottom: 0;
    line-height: 1.3rem;
    padding-left: 0.5rem;
    font-size: 0.38rem;
    border-top: 1px solid #eaeaea;

    span {
      color: red;
      font-size: 0.4rem;
    }

    .left {
      float left;

    }

    .right {
      float right

      width: 2.3rem;
      height: 0.9rem;
      line-height: 0.9rem;
      font-size: 0.35rem;
      margin-top: 0.2rem;
      margin-right: 0.3rem;
      border-radius: 3px;
      text-align: center;
      color: #fff;
      background: #69c1ff;
      float: right;
    }

    .cancel {
      color: #fff;
      background: #e3e3e3;
    }
  }
}
</style>



