<template>

  <div class="login">
    <div class="header-login">
      <h3>快捷登录</h3>
    </div>
    <div class="AuthorMain">
      <van-cell-group>
        <van-field v-model="mobile" placeholder="请输入手机号" type="number"/>

        <van-field
          v-model="code"
          center
          clearable
          type="number"
          placeholder="请输入短信验证码"
        >
          <van-button slot="button" size="small" type="primary" @click="sendSMS" :disabled="btnSendStatus">{{btnSendText}}</van-button>
        </van-field>
      </van-cell-group>

      <div>
        <van-button square size="large" type="primary" @click="login">登 录</van-button>
      </div>

      <van-button plain square size="large" type="default" @click="goHome">返回首页</van-button>

    </div>
  </div>
</template>

<script>
  import {Toast} from "vant";
  import api from '@/api';
  import weixi from '@/utils/weixi'

  export default {

    data() {
      return {
        mobile: '',
        code: '',
        btnSendText: '发送验证码',
        timeOut: 0,
        btnSendStatus: false,
        weixiUser: {}
      }
    },
    created: function(){
      var _this = this;
      this.weixiUser = weixi.getWeixiUser();
      Toast.clear();
    },
    methods: {
      goHome: function () {
        this.$router.push({
          path: "home",
        })
      },
      sendSMS: function () {

        if (!this.validate.validatePhone(this.mobile)){
          Toast("手机号码不正确！");
          return;
        }

        this.btnSendStatus = true;
        this.timeOut = 60;
        this.delayShow();
        this.timer = setInterval(() => {
          this.delayShow();
        }, 1000)

        api.sendSMSCode(this.mobile).then(function ({data}) {

        })
      },
      delayShow:function () {

        if (this.timeOut <=0){
          clearInterval(this.timer)
          this.btnSendStatus = false;
          this.btnSendText =  "发送验证码";
          return;
        }

        this.btnSendText = this.timeOut+"s后重发";
        //console.log(this.timeOut+"S");
        this.timeOut--;
      },
      login:function () {

        var _this = this;

        if (!this.validate.validatePhone(this.mobile)){
          Toast("手机号码不正确！");
          return;
        }

        if (this.code == ""){
          Toast("请输入验证码！");
          return;
        }

        //var user = weixin.getWeixiUser()

        var data = {
          mobile: this.mobile,
          code: this.code,
          openId: weixi.getOpenId(),
          accessToken: weixi.getAccessToken(),
        }
        api.loginByCode(data).then(function({data}){
          if (data.errno == 0){

            console.log(data)
            sessionStorage.setItem('X-Token',data.data.token);
            sessionStorage.setItem('token-expire',data.data.tokenExpire);

            _this.$router.go(-1);

          }
          else{
            Toast(data.errmsg)
          }
        })
      }
    }
  }
</script>

<style>
  .login {
    margin: 5px 10px 5px;
  }

  .header-login {
    padding: 20px 15px 10px;
    font-size: 0.7rem;
  }

  .van-button {
    margin: 5px 0 0px;

  }

  .van-button--large {
    width: 100%;
    height: 40px;
    line-height: 40px;
  }


</style>



