<script>
  import {MessageBox} from "mint-ui";
  import { Toast } from 'vant';
  import PayHeader from "../../common/header";
  import api from '@/api';
  import area from '@/utils/area'

export default {
  name: "pay",
  data() {
    return {
      listIndex: 0,
      invoiceIndex: 0,
      pay: this.$store.state.payGoods,
      addressId: this.$store.state.address,
      goodsAmount:0, //商品金额
      orderAmount: 0, //订单总金额
      address: {}, //收货地址
      noAddress: true,
      freight: 8,   //运费
      freightPrice: 8,//运费标准
      freightText: '',//运费显示内容
      freeFreight: 88, //满免运费
      pointPrice: 100, //积分比例
      remarks: this.$store.state.orderRemarks,
      delivery: 0,  //送货方式
      userInfo:{},
      integralPrice:0, //积分减免
      maxIntegralPrice: 0, //积分最高减免
    };
  },
  components: {
    PayHeader,
  },
  methods: {
    btn(id, index) {
      this.listIndex = index;
    },
    invoiceClick(index) {
      this.invoiceIndex = index;
    },
    paySelect(index) {
      this.delivery = index;
      this.calculateOrder();
    },
    jsPoint(){
      if (this.maxIntegralPrice>0 && this.integralPrice >this.maxIntegralPrice){
        Toast("积分抵扣金额不能大于付款金额!");
        this.integralPrice = this.maxIntegralPrice
      }
      this.calculateOrder();

    },
    /**
     * 计算订单
     */
    calculateOrder(){
      var _this = this;

      if (_this.delivery == 0){
        if(_this.goodsAmount >= _this.freeFreight){ //免运费
          _this.freight = 0;
          _this.freightText = "免运费";
        }
        else{
          _this.freight = _this.freightPrice;
          _this.freightText = "¥"+_this.freight.toFixed(2);
        }
        var amount = _this.goodsAmount + _this.freight - _this.integralPrice;

        if (amount <0){
          _this.integralPrice = parseInt(_this.integralPrice) + amount;
          _this.orderAmount = 0;
        }
        else{
          _this.orderAmount = amount;
        }
      }
      else{
        //自取
        var amount = _this.goodsAmount - _this.integralPrice;
        _this.freight = 0;
        if (amount <0){
          _this.integralPrice += amount;
          _this.orderAmount = 0;
        }
        else{
          _this.orderAmount = amount;
        }
        _this.freightText = "免运费";
      }

    },
    /**
     * 全部抵扣
     */
    onAllPoint(){
      var allPointPrice = (this.userInfo.point / this.pointPrice).toFixed(2);
      if (allPointPrice >= this.orderAmount){
        this.integralPrice = this.orderAmount;
      }
      else {
        this.integralPrice = allPointPrice;
      }

      this.calculateOrder();
    },
    /**
     * 下订单
     * @param id
     * @param index
     */
    onAddOrder() {

      //判断有没有选择地址
      if (this.delivery == 0 && this.address.mobile == undefined){
        Toast.fail("请选择收货地址!")
        return;
      }

      //积分抵扣
      if (this.integralPrice >0){

        if (this.integralPrice * this.pointPrice > this.userInfo.point){
          Toast.fail("您的积分不足!");
          return
        }
      }

      MessageBox.confirm("确认下单?").then(action=>{
        if (action == 'confirm'){
          this.addOrder();
        }
      })
    },
    addOrder(){

      Toast.loading({
        mask: true,
        message: '处理中...'
      });

      var _this = this;
      var order = {
        addressDetail: this.address.detailedAddress,
        mobile: this.address.mobile,
        consignee: this.address.name,
        freight: this.freight,
        integralPrice:this.integralPrice,
        remarks: this.remarks,
        delivery: this.delivery,
        goods: [],
      }

      this.pay.forEach((goods, index) => {

        order.goods.push({
          goodsId: goods.goodsId,
          productId: goods.productId,
          quantity: goods.quantity,
        })
      })
      console.log(order)
      api.orderSubmit(order).then(function({data}){

        if (data.errno != 0){
          Toast.clear();
          Toast(data.errMsg);
        }
        else{
          _this.$store.commit('cleanPayGoods');
          Toast.clear();
          _this.$router.push({
            path: '/weixiPay',
            query: {orderId:data.data.orderId,amount:_this.orderAmount}
          })
        }
      })
    },
    selectAddress(){
      this.$store.commit('setOrderRemarks',this.remarks);
      this.$router.push({
        path:"address",
        query:{from:'pay'}
      })
    }
  },
  created() {
    var _this = this;
    this.goodsAmount = 0;

    this.pay.forEach((goods, index) => {

      this.goodsAmount += goods.price * goods.quantity;

    })

    this.orderAmount = this.goodsAmount + this.freight;
    //取收货地址
    api.getDefaultAddress(_this.addressId).then(function({data}){

      console.log(_this.addressId);


      console.log(data,_this.address)

      if (data.data != undefined) {

        _this.address = data.data

        _this.noAddress = false;

        if (_this.addressId == null) {
          _this.address.detailedAddress = area.province_list[_this.address.provinceId] + " "
            + area.city_list[_this.address.cityId] + " "
            + area.county_list[_this.address.areaId] + " "
            + _this.address.address;
        }
        else {
          _this.address.detailedAddress = area.province_list[_this.address.provinceId] + " "
            + area.city_list[_this.address.cityId] + " "
            + area.county_list[_this.address.districtId] + " "
            + _this.address.address;
        }
      }
    })

    api.payInfo().then(function({data}){
      if (data.errno == 0){

        _this.userInfo = data.data.user_info;
        _this.freightPrice = data.data.freight_price;
        _this.freight = _this.freightPrice;
        _this.freeFreight = data.data.free_freight;
        _this.pointPrice = data.data.point_price;

        _this.calculateOrder();
      }
    })

  },
}
</script>





