<template>
  <div>
<p>openid:{{openid}}</p>
  <p>token:{{token}}</p>
  <p>user:{{user}}</p>
  <mt-button @click="cleanStorage">clean Storeage</mt-button>
  </div>
</template>

<script>
    export default {
      name: "index",
      data() {
        return {
          openid: localStorage.getItem("openid"),
          token: localStorage.getItem("X-Token"),
          user: localStorage.getItem("user"),
        }
      },
      mounted(){

        this.getData();
      },
      methods:{
        getData:function () {
          this.openid = localStorage.getItem("openid");
          this.token = localStorage.getItem("X-Token");
          this.user = localStorage.getItem("user");
        },
        cleanStorage:function () {
          localStorage.clear();
          this.getData();
        }
      }
    }
</script>

<style scoped>

</style>



