<template>
    <div class="my">
       <!-- <My-Header title="个人中心"></My-Header>-->
      <div class="container">
        <div class="container-bj">
          <img :src="user.avatarUrl">
          <span class="user-name">{{user.nickName}}</span>
          <!--<p>不要被人言左右，要相信自己的判断</p>-->
          <div class="container-integral">
            <p>
              <span>0</span>
              <span>优惠券</span>
            </p>
            <p>
              <span>0</span>
              <span>会员卡</span>
            </p>
            <router-link tag="p" to="/pointList">
              <span>{{user.point}}</span>
              <span>积分</span>
            </router-link>
          </div>
        </div>

        <router-link class="container-order" to="/order" tag="div">
          <div class="container-order-1">
            <p class="left">我的订单</p>
            <p class="right">全部订单 ></p>
          </div>
          <div class="container-order-2">
            <p class="">
              <img src="/static/img/111.png">
              <span >待付款<i class="badge" v-show="orders.unpaid>0">{{orders.unpaid}}</i></span>
            </p>
            <p class="">
              <img src="/static/img/444.png">
              <span >待发货<i class="badge" v-show="orders.unshipments>0">{{orders.unshipments}}</i></span>
            </p>
            <p class="">
              <img src="/static/img/222.png">
              <span >待收货<i class="badge" v-show="orders.unconfirm>0">{{orders.unconfirm}}</i></span>
            </p>
            <p class="">
              <img src="/static/img/333.png">
              <span >待评价<i class="badge" v-show="orders.unestimate>0">{{orders.unestimate}}</i></span>
            </p>
     <!--       <p class="">
              <img src="/static/img/444.png">
              <span >已取消<i class="badge" v-show="orders.cancel>0">{{orders.cancel}}</i></span>
            </p>-->
          </div>
        </router-link>

        <div class="container-con" id="transition">
          <router-link tag="div" to="/myCollection" class="con">
            <div class="con-left">
              <i class="iconfont icon-collection"></i>
              <span>我的收藏</span>
            </div>
            <div class="con-rigth">
              <i class="iconfont icon-youjiantou"></i>
            </div>
            <div style="clear:both;"></div>
          </router-link>

          <router-link tag="div" to="/address" class="con" >
            <div class="con-left">
              <i class="iconfont icon-shouhuodizhi"></i>
              <span>我的收货地址</span>
            </div>
            <div class="con-rigth">
              <i class="iconfont icon-youjiantou"></i>
            </div>
            <div style="clear:both;"></div>
          </router-link>

          <router-link tag="div" to="/cart" class="con">
            <div class="con-left">
              <i class="iconfont icon-gouwuche"></i>
              <span>我的购物车</span>
            </div>
            <div class="con-rigth">
              <i class="iconfont icon-youjiantou"></i>
            </div>
          </router-link>

          <router-link tag="div" to="/pointList" class="con">
            <div class="con-left">
              <i class="iconfont icon-share_icon"></i>
              <span>积分记录</span>
            </div>
            <div class="con-rigth">
              <i class="iconfont icon-youjiantou"></i>
            </div>
          </router-link>

        </div>
      </div>
      <v-footer></v-footer>
    </div>
</template>

<script>
import footer from '../../pages/footer'
import api from '@/api'

export default {
  name:"my",
  data(){
      return{
        user: {},
        orders: {
          unpaid:0,
          unconfirm:0,
          uncomment:0,
          cancel:0
        },
      }
  },
  components:{
      "v-footer":footer
  },
  mounted() {
    var _this = this;
    api.myInfo().then(function ({data}) {

      if (data.errno === 401){
        _this.$router.push({
          path:'login'
        });
      }
      else{
        _this.user = data.data.user;
        _this.orders = data.data.order;

      }
    });
  },
  methods:{
      order:function(){
         this.$router.push('/order')
      },
      author:function(){
          this.$router.push('/author')
      }
  }
}
</script>

<style lang="stylus" scoped>
  .container-order{
    width 100%
    height 3.5rem
    background white
    display block
    margin-bottom .15rem
    /*margin-top .15rem*/
    font-size 0.35rem
    .container-order-1{
      width 100%
      height 1.5rem
      .left{
        float: left;
        display: block;
        line-height: 1.5rem;
        margin-left: .5rem;
        font-size: 0.4rem;

      }
      .right {
        float: right;
        line-height: 1.5rem;
        margin-right: .6rem;
        font-size: 0.4rem;
      }
    }
    .container-order-2{
      width 100%
      height 1.5rem
      display flex
      justify-content center
      align-items center

      p{
        width 25%
        display flex
        flex-direction column
        margin-top .3rem

        img{
          width .8rem
          height .8rem
          margin auto
        }
        span{
          text-align center
          padding-top .3rem
        }
      }
    }
  }
  .t{
    background-image url('/static/img/t.png');
  }

  .badge {
    width: 15px;
    height: 15px;
    position: relative;
    display: inline-block;
    top: -10px;
   /* left: 62px;*/
    background: #f81301;
    border-radius: 50%;
    text-align: center;
    line-height: 15px;
    color: #fff;
    font-style: normal;
    font-size: xx-small;
  }

  .container {
    width: 100%;
    height: 5rem;
    position: absolute;
    /*top: 1.45rem;*/
    .container-bj {
      width: 100%;
      height: 100%;
      background: url('/static/img/bj.png') no-repeat;
      background-size: 100% 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding-top: 0.5rem;

      img {
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
      }
      .user-name {
        font-size: 0.5rem;
      }

      span {
        color: #ffffff;
        /*font-size: 0.5rem;*/
      }


      p {
        font-size 0.35rem;
        color: #ffffff;
      }
    }
    .container-integral{
      width 100%
      height 2rem
      /*background #ffffff*/
      display: flex;
      justify-content: center
      p{
        width 33%
        height 100%
        font-size .36rem
        line-height .6rem
        font-weight 500
        float left
        display flex
        flex-direction column
        text-align center
        justify-content: center;
      }
    }

    .container-con {
      margin-bottom 1.45rem
      .con {
        width: 100%;
        height: 1.41rem;
        background: #ffffff;
        border-bottom: 1px solid #f0f0f0;
        display: block;

        .con-left {
          float: left;
          line-height: 1rem;
          padding-left: 0.47rem;
          width: 90%;
          i {
            font-size: 0.5rem;
          }

          span {
            font-size: 0.37rem;
            padding-left: 0.1rem;
          }
        }

        .con-rigth {
          float: right;
          line-height: 1rem;
          padding-right: 0.4rem;
        }
      }
    }
  }
</style>



