<template>
  <div>
    <my-header title="积分记录"></my-header>
    <div class="container1">
      <div class="mypoint">
        我的积分 {{userInfo.point}}
      </div>
      <div>
        <ul v-infinite-scroll="loadPointList"
             infinite-scroll-disabled="loading"
             infinite-scroll-distance="10">
          <li v-for="list in pointList">
            <div class="pointlist">
              <div class="left">
                <div>
                  <div ><span class="refText">{{list.refText}}</span></div>
                  <!--<p>来源单号:{{list.refId}}</p>-->
                  <p class="time">{{list.addTime|formatDate}}</p>
                </div>
              </div>
              <div class="right" :class="{'kouqu':list.change==1,'xinzen':list.change==2}">
                <span class="change">{{list.changeText}}</span><span> {{list.point}}</span>
              </div>
              <div style="clear: both"></div>
            </div>

          </li>
        </ul>
        <div style="padding:5px" v-show="!loading">
        <mt-spinner :size="20" :type="3" color="#26a2ff"></mt-spinner>
        </div>
      </div>

    </div>
  </div>

</template>

<script>
  import myHeader from "../../common/header";
  import api from '@/api'
  import weixi from '@/utils/weixi'


  export default {
    name: "pointList",
    data(){
      return {
        pointList:[],
        userInfo: {},
        loading: false,
        pagenum: 1,
        pagesize: 10
      }
    },
    components:{
      myHeader
    },
    created(){
      this.loadPointList();
      var _this = this;
      weixi.getUserInfo().then(function (data) {
        _this.userInfo = data.data;
      });

    },
    methods:{
      loadPointList: function(){
        this.loading = true;
        var params = {page:this.pagenum++,size:this.pagesize}
        var _this = this;
        api.pointList(params).then(({data})=>{
          if (data.errno == 0){
            //console.log(_this.pointList.length,data.data.data.length,data.data.count)
            if (_this.pointList.length+data.data.data.length >= data.data.count ){
              _this.loading = true;
            }
            else{
              _this.loading = false;
            }
            data.data.data.forEach((list)=>{
              list.changeText = list.change == 1 ? "+" : "-";

              _this.pointList.push(list)
            });
          }

        })
      },
    }
  }
</script>

<style lang="stylus" scoped>
  .container1 {
    padding-top: 1.45rem;

    background-color:white;

    .mypoint {
      width: 100%;
      height:4rem;
      line-height: 4rem;
      background-color #4CAF50;
      vertical-align:middle
      color: white;
      font-size:0.8rem;
      text-align: center;
      /*background: url(/static/img/bj.jpg) no-repeat 100%;*/
    }

    .pointlist {
      border-bottom 1px solid #f0f0f0
      //border-top 1px solid #f0f0f0
      /*height: 0.8rem;*/
      padding:3px 10px 3px;

      .left {
        float: left;

        .refText {
          background-color #3283fa;
          text-align:center
          /*width: 200px;*/
          padding:3px 5px 3px;
          color:white;
          font-size:0.35rem;
          border-radius: 5px;
        }
        .time {
          font-size :0.3rem;
          color: rgba(100,100,100,0.6);
          padding-top:10px;
        }
      }
      .right {
        float: right;
        width: 2rem;
        font-size: 0.4rem;
        text-align :right;
        height: 1.5rem;
        line-height: 1.5rem;
        .change{
          font-size:0.4rem;
        }
      }
      .kouqu {
        color: #ef4f4f;
      }
      .xinzen {
        color: #06bf04;
      }
    }

    img {
      width:5rem;
      padding-top:1rem;
    }


  }
</style>



