<template>
	<div class="bg">
    <section class="topic">
            <!--<div id="topLink"><a href="./calendar-book-new.html?activeId=799" class="calendar-link">签到攻略</a> </div>
-->
            <div class="sign-in-btn"><p class="lh" @click="onSignIn"><label v-if="!isSign">立即签到</label><label v-else>已签到</label></p></div>
    </section>
    <section class="page-wrapper">
      <!--sign in calendar-->
      <section class="calendar">
        <header class="calendar-tit">
          <section class="calendar-tit-con">
            <div class="calendar-time" id="nowDate" style="text-align:center">
              <div class="time">
                <i class="icn"></i><span class="calendar-year">{{today}}</span>
              </div>
            </div>
            <div class="calendar-notice" style="display: none;">
              <span class="ntxt">签到提醒:</span>
              <span class="desc" id="desc"></span>
              <label class="ui-switch"><input type="checkbox" id="btnChk"></label>
            </div>
          </section>
          <section class="rec-icon">
            <span class="recon pl"></span>
            <span class="recon pr"></span>
            <span class="test"></span>
          </section>
        </header>
        <div class="calendar-table">

          <Calendar v-on:choseDay="clickDay"
                    :sundayStart=true :is_hide_otherday="true" :markDate =markDate ></Calendar>
        </div>

      </section>

    </section>
    <section class="activity-rule">
      <header class="activity-rule-tit">
        <h3 class="activity-rule-tit-con" id="actRuleTiltle">签到规则</h3>
      </header>
      <section class="activity-rule-con" id="actRule">
        <p class="red">1、每次签到可以获取20积分；积分自动发放至用户账户。</p><!--连续签到6天奖励50积分；连续签到15天奖励100元积分；-->
      </section>
    </section>
    <div v-show="success">
      <div class="bg1"></div>
      <div class="success">
        <img class="img"src="/static/img/sucessful.png">
        <p>+{{point}}积分</p>
        <img class="close" src="/static/img/close-circle-o.png" @click="onClose">
      </div>
    </div>
    <div class="back" @click="back">
      <i class="iconfont icon-zuojiantou"></i>
    </div>



	</div>

</template>

<style lang="stylus" scoped>

  .bg{
    background-color white;
  }


  .bg1 {
    position: fixed;
    z-index: 999;
    top: 0;
    height:100%;
    width:100%;
    background-color: #000000;
    /*filter:alpha('opacity:50'); */
    opacity: 0.5;
    -moz-opacity: 0.5;
    -khtml-opacity: 0.5;
  }

  .success {
    position: fixed;
    z-index: 1000;
    top:0;
    /*left: 1.5rem;*/
    .img {
      width: 100%;
    }
    p{
      text-align: center;
      color: #fada33;
      /*background-color: #fff;*/
      width: 203px;
      height: 80px;
      left: 25%;
      top: -64px;
      line-height: 80px;
      border-radius: 10px;
      position: relative;
    }
    .close{
      position: relative;
      width: 0.7rem;
      top: -9rem;
      left: 7rem;
    }
  }

  .back {
    position: absolute;
    z-index: 1000;
    top: 0.3rem;
    left: 0.3rem;
    width: 0.7rem;
    height: 0.7rem;
    line-height: 0.7rem;
    font-size: 0.2rem;
    border-radius: 50%;
    background-color: #5a5a5a;
    text-align: center;
    /* vertical-align: middle; */
    color: #fff;
    -moz-opacity: 0.5;
    opacity: 0.5;
    padding-right: 4px;
  }
  .topic {
    background-size: 100% 100%;
    height: 4.5rem;
    position: relative;
    background-image: url('/static/img/sign_bg.jpg');
    /*padding-bottom: 0.6rem;*/

    .sign-in-btn {
      display: block;
      position: absolute;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      top: 2.5rem;
      text-align: center;
      font-size: 0.55rem;
      line-height: 100%;
      background: -webkit-linear-gradient(left, #f76aa5, #8d73e2);
      background: linear-gradient(to right, #f76aa5, #8d73e2);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      /*width: 1.08rem;*/
      height: 1.08rem;
      z-index: 99;

    }
  }
  .page-wrapper {
    max-width: 95%;
    margin: 0 auto;
  }
    .calendar {
      /*font-size: .15rem;*/
      .calendar-tit {
        padding: .13rem .13rem .22rem;
        background-color: #fd5fb7;
        color: #fff;
        border-top-left-radius: .225rem;
        border-top-right-radius: .225rem;

       .icn {
          width: .34rem;
          height: .34rem;
          background: url('data:image/png;base64,') no-repeat;
          background-size: contain;
          display: inline-block;
          /*vertical-align: text-bottom;*/
          margin-right: .15rem;
        }
        .calendar-year{
          font-size: 0.35rem;
        }
      }

      .calendar-con {
        padding: .3rem .15rem;
        border: 1px solid #f7f7f7;
        border-top-width: 0;
        border-bottom-left-radius: .23rem;
        border-bottom-right-radius: .23rem;
        position: relative;
      }


      .calendar-table {
        border: 0;
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
        margin-top: .1rem;

        td,th {
          text-align: center;
          font-size: .36rem;
          height: .8rem;
          line-height: .4rem;
          margin: 0;
          padding: 0;
          color: #4a4a4a;
          width: 14.2857%;

          span {
            width:0.8rem;
            height:0.8rem;
            line-height:0.8rem;
            display:inline-block;
          }

          .next,.prev {
            color: #ddd;
          }
        }
      }

      .sign-active {
        background: url('data:image/png;base64,') 126% 16% no-repeat;
        background-size: .7rem .7rem;
        color: #33a255;
      }



    }

  .activity-rule {
    padding-bottom: .45rem;

    .activity-rule-tit {
      margin: .4rem .13rem .35rem;
      height: 0;
      border-bottom: 1px dashed #ddd;
      position: relative;

      .activity-rule-tit-con {
        position: absolute;
        /* width: 1.55rem; */
        ght: 0.3rem;
        left: 50%;
        margin-left: -0.75rem;
        top: 50%;
        margin-top: -0.15rem;
        font-size: 0.34rem;
        color: #4a4a4a;
        line-height: 0.3rem;
        text-align: center;
        background: #fff;
        padding-left: 10px;
        padding-right: 10px;

      }
    }

    .activity-rule-con {
      margin: 0 .33rem;
      color: #4a4a4a;
      font-size: .34rem;

      .red {
        color:red;
      }

      p{
        margin-bottom :0.1rem;
      }
    }


  }


</style>
<script>
  import Calendar from 'vue-calendar-component';
  import api from '@/api'
  export default {

    data() {
      return {
        show: false,
        success: false,
        today: '2018-10-24',
        markDate: [],
        isSign: false,
        point: 0 ,
      }
    },
    components: {
      Calendar
    },
    created:function(){

      var _this = this;
      this.today = this.moment().format('YYYY-MM-DD');
      var beginDate=this.moment().add('month',-1).format("YYYY-MM")+'-01';
      var endDate=this.moment().add('month',1).add('days',-1).format('YYYY-MM-DD');
      var params = {beginDate:beginDate,endDate:endDate}
      console.log(params)
      api.signList(params).then(function ({data}) {
        if (data.errno == 0){
          var list = data.data.data;
          console.log(list)
          if (list.length>0) {
            _this.markDate = list.map(list => {
              if (list.signDate == _this.today) {
                _this.isSign = true;
              }
              return list.signDate;
            })
            console.log(_this.markDate)
          }
        }
      })
    },
    mounted: function () {

    },
    methods: {
      back() {
        this.$router.go(-1);
      },
      onSignIn(){
        if (this.isSign){
          return;
        }
        var _this = this;
        api.signIn().then(({data})=>{
          if (data.errno == 0){
            _this.success = true;
            _this.point = data.data.point;
            _this.isSign = true;
            _this.markDate.push(_this.today)
          }
        })
      },
      onClose(){
        this.success = false;
      },
      clickDay(data){
        console.log(data);
      }

    }
  }
</script>



