<template>
    <div>
        <Address-Header title="管理收货地址"></Address-Header>
        <Loading v-if="isLoading"></Loading>
        <div class="Address-box" v-else>
            <div class="address-one">
                <p class="left">配送地址</p>
                <p class="right" @click="goAdd_address">+添加地址</p>
            </div>
            <div class="pay-address" v-for="(list,index) in address" :key="index"  > <!--:class="{active:list.id===$store.state.address}"-->
                <div class="address-box" @click="selectAddress(list.id)">

                    <span class="name"> {{list.name}}</span>
                    <span class="">{{list.mobile}}</span>
                    <span class="default" v-show="list.isDefault">默认</span>

                  <p class="address-details">{{list.detailedAddress}}</p>
                </div>
                <div class="right">
                  <span class="phone">
                        <i class="iconfont icon-bianji" @click="edit(list.id)"></i>
                        <i class="iconfont icon-lajitong" @click="remove(list.id)"></i>
                    </span>
                </div>

            </div>
        </div>
    </div>
</template>


<script>
import AddressHeader from "../common/header";
import { MessageBox } from "mint-ui";
import api from '@/api';
import area from '@/utils/area'

export default {
  data() {
    return {
      nowIndex: 0,
      address: [],
      isLoading: true,
      from: this.$route.query.from,
    }
  },
  components: {
    AddressHeader
  },
  created() {

    this.loadAddress();
  },
  methods: {

    loadAddress() {
      var _this = this;
      api.addressList().then(function ({data}) {

        if (data.errno == 0) {
          _this.address = data.data.map(addr => {
            addr.detailedAddress = area.province_list[addr.provinceId] + " "
              + area.city_list[addr.cityId] + " "
              + area.county_list[addr.areaId] + " "
              + addr.address;
            return addr;

          });

          _this.isLoading = false;

        }
      });
    },

    goAdd_address() {
      this.$router.push({
        path: "add_address"
      });
    },
    selectAddress(id) {
      if (this.from == 'pay') {
        this.$store.commit('setAddress', id);

      }
      this.$router.go(-1);
    },
    edit(id) {
      this.$router.push({
        path: "add_address",
        query: {id: id}
      })
    },
    remove(id) {
      var _this = this;
      MessageBox.confirm('是否删除该条地址?').then(action => {
        var data = {id: id}
        api.removeAddress(data).then(function ({data}) {
          _this.loadAddress();
        })
      });
    }
  }
};
</script>


<style lang="stylus" scoped>
  .active {

  }

  .Address-box {
    padding-top: 1.45rem;

    .address-one {
      width: 100%;
      height: 1.6rem;
      line-height: 1.6rem;
      background: #ffffff;
      margin-bottom: 0.2rem;
      border-top: 1px solid #eaeaea;

      .left {
        float: left;
        margin-left: 0.6rem;
        font-size: 0.37rem;
      }

      .right {
        float: right;
        margin-right: 0.6rem;
        font-size: 0.37rem;
        color: #00acff;
      }
    }
    .active {
      background: #00a0e9 26 !important;
    }

    .pay-address {
      width: 100%;
      height: 2.3rem;
      background: #ffffff;
      background-size: 1.6rem;
      margin-top: 0.3rem;
      display: block;

      .address-box {
        width: 85%;
        margin: auto;
        font-size: 0.4rem;

        padding: 0.5rem 0 0.3rem 0.5rem;
        float: left;

        .phone {
          float: right;
        }
        .default {
          font-size: x-small;
          background: #00a0e9;
          padding: 2px 8px;
          border-radius: 14px;
          color: #fff;
        }
        .address-details {
          /*width: 87%;
          height: 1rem;*/
          margin-top: 10px;
          color: #666;
          font-size: 0.38rem;
        }
      }
      .right {
        float: right;
        padding-right:10px;
      }

      .address-operation {
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        margin-top: 0.3rem;

        .address-operation-box {
          width: 100%;
          border-top: 1px solid #eaeaea;

          i {
            float: right;
            font-size: 0.5rem;
            margin-right: 1.2rem;
            color: #a3a3a3;
          }
        }
      }
    }
  }
</style>



