import Vue from 'vue'
import Router from 'vue-router'

import home from '@/pages/home/home'
import cart from '@/pages/cart/cart'
import my from '@/pages/my/my'
import goodDetail from '@/pages/detail/goodDetail'
import order from '@/pages/order/order'
import author from '@/pages/author'
import choose from '@/pages/choose'
import news from '@/pages/news/news'
import newsDetail from '@/pages/news/newsDetail'
import parts from '@/pages/parts'
import change from '@/pages/detail/change'
import success from '../common/success'
import myCollection from '@/pages/my/myCollection'
import classify from "@/pages/classify/classify"
import orderDetails from "@/pages/order/orderDetails"
import pay from "@/pages/pay/pay"
import address from "@/pages/address"
import add_address from "@/pages/add_address"
import login from "@/pages/login"
import coupon from "@/pages/coupon"
import pointList from "@/pages/my/pointList"
import sign from "@/pages/my/sign"
import reg from "@/pages/reg"
import index from "@/pages/index"
import weixiPay from "@/pages/pay/weixiPay"

Vue.use(Router)

var router = new Router({
  mode: 'history',
  routes: [
    { path: '/', name: 'home', component: home },
    { path: '/home', name: 'home', component: home,meta:{index:1} },
    { path: '/cart', name: 'cart', component: cart },
    { path: '/my', name: 'my', component: my },
    {
      path: '/goodDetail',
      name: 'goodDetail',
      component: goodDetail,
      meta:{index:0}
    },
    {
      path: '/order',
      name: 'order',
      component: order,
      children:[
       {
        path: '/order/orderDetails',
        name: '/order/orderDetails',
        component: orderDetails,
       }
      ]
    },
    { path: '/author', name: 'author', component: author },
    { path: '/choose', name: 'choose', component: choose },
    { path: '/newsDetail', name: 'newsDetail', component: newsDetail },
    { path: '/news', name: 'news', component: news },
    { path: '/parts', name: 'parts', component: parts },
    { path: '/change', name: 'change', component: change },
    { path: '/success',name:'success',component:success},
    { path: '/MyCollection',name:'MyCollection',component:myCollection},
    { path: '/classify',name:'classify',component:classify},
    { path: '/OrderDetails',name:'OrderDetails',component:orderDetails},
    {
      path: '/pay',
      name:'pay',
      component:pay,
      /*meta: {
        keepAlive: true // 需要被缓存
      }*/
    },
    {
      path: '/address',
      name:'address',
      component:address
    },
    {
      path: '/add_address',
      name:'add_address',
      component:add_address
    },
    { path: '/login', name: 'login', component: login },
    { path:'/coupon' ,name:'coupon', component: coupon},
    { path:'/pointList' ,name:'pointList', component: pointList},
    { path:'/sign' ,name:'sign', component: sign},
    { path:'/reg' ,name:'reg', component: reg},
    { path:'/index' ,name:'index', component: index},
    { path:'/weixiPay' ,name:'weixiPay', component: weixiPay},
  ],
  //路由切换页面始终回到最顶部
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

export default router;



