<template>
  <div class="content1" v-if="!regSuccess">
    <img src="../assets/t1.png" alt="" class="img1">
    <img src="../assets/t2.png" alt="" class="img2">
    <img src="../assets/t3.png" alt="" class="img3">
    <img src="../assets/t4.png" alt="" class="img4">{{member.openId}}
    <div class="inputView1">
      <div>
        <img src="../assets/t5.png" alt="" class="img5">
        <input type="text" class="input1" v-model="member.trueName">
      </div>
    </div>
    <div class="inputView2">
      <div>
        <img src="../assets/t6.png" alt="" class="img6">
        <input type="number" class="input1" v-model="member.phone" oninput="if(value.length > 11)value = value.slice(0, 11)">
      </div>

    </div>
    <span class="span1" v-text="showText" @click="sendMsg"></span>
    <div class="inputView3">
      <div>
        <img src="../assets/t7.png" alt="" class="img7">
        <input type="number" class="input2" v-model="member.code" oninput="if(value.length > 4)value = value.slice(0, 4)">
      </div>
    </div>
    <input type="button" class="reg" value="注 册" @click="submit"/>
    <img src="../assets/t8.png" alt="" class="img8" >
  </div>
  <div v-else>
    <div class="success">
      <i class="iconfont icon-chenggong1"></i>
      <img src="../assets/t2.png" alt=""  style="margin:0 auto">
      <h2>注册成功</h2>
      <p>恭喜您，会员注册成功 感谢您的支持与厚爱
      </p>
    </div>
  </div>
</template>

<script>
  import { Toast,MessageBox,Indicator } from 'mint-ui';
  import weixi from '@/utils/weixi.js'


  export default {
    name: 'reg',
    data() {
      return {
        member:{
          trueName:"",
          phone:"",
          code:"",
          openId:""
        },
        showText: '获取验证码',
        timeOut:0,
        timer:null,
        isSendCode: false,
        regSuccess: false,
      }
    },
    mounted(){
      // $('.content').height($(window).height());
      //console.log(this.$route.query.openid)
      this.member.openId = weixi.checkWx(this.$route);
      Toast(this.member.openId)

    },
    methods:{
      sendMsg: async function () {


        if (!this.validatePhone(this.member.phone)){
          return false;
        }

        this.timeOut = 60;
        this.delayShow();
        this.timer = setInterval(() => {
          this.delayShow();
        }, 1000)


        this.$http({
          url: this.api.member.SendMsm,
          method: 'get',
          params: this.$http.adornParams({phone:this.member.phone})
        }).then(({data}) => {
          console.log(data)
          if (data.code != 0){
            Toast(data.msg)
          }
        })

      },
      delayShow:function () {

        if (this.timeOut <=0){
          clearInterval(this.timer)
          this.showText = "获取验证码";
          return;
        }

        this.showText = this.timeOut+"s后获取";
        console.log(this.timeOut+"S");
        this.timeOut--;
      },
      //验证手机号码部分
      validatePhone: function(phone){
        var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
        if(phone==''){
          Toast("请输入手机号码");
          return false;
        }else if(!reg.test(phone)){
          Toast("手机格式不正确");
          return false;
        }
        return true;
      },
      validate: function () {

        if (this.member.trueName == ""){
          Toast("请输入姓名!");
          return false;
        }

        if (!this.validatePhone(this.member.phone)){
          return false;
        }

        if (this.member.code == ""){
          Toast("请输入验证码!");
          return false;
        }
        return true;
      },
      submit: async function () {
       /* alert(JSON.stringify(this.member));
        alert(weixi.checkWx(this.$route))
        alert(sessionStorage.getItem("openid"))*/
        if (!this.validate()){
          return
        }

        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });

        const res = await this.http.postJson(this.api.member.reg,this.member);

        this.$http({
          url: this.api.member.reg,
          method: 'post',
          data: this.$http.adornData(this.member)
        }).then(({data}) => {

          if (data.code != 0){
            Toast(data.msg)
          }
          else{
            this.regSuccess = true;
          }
        }).finally(()=>{
          Indicator.close();
        })

      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .content1 {
    background-color: #fff;
  }
  img{
    display: table-cell;
    vertical-align: middle;
  }
  .content1 > img {

    position: absolute;
  }

  .content1 > .img1 {
    top: 0;
    left: 0;
    width: 60px;
  }

  .content1 > .img2 {
    top:60px;
    left: 50%;
    width: 135px;
    margin-left: -67.25px;
  }

  .content1 > .img3 {
    top:110px;
    left: 50%;
    width: 135px;
    margin-left: -67.25px;
  }

  .content1 > .img4 {
    top: 180px;
    left: 50%;
    width: 170px;
    margin-left: -85px;
  }

  .content1 > div {
    position: absolute;
    overflow: hidden;
    width: 170px;
    left: 50%;
    margin-left: -85px;
  }

  .inputView1 {
    float: right;
    top: 300px;
  }

  .inputView2 {
    top: 330px;
  }

  .inputView3 {
    top:360px;
  }
  .content1 > div >div{
    position: relative;
    overflow: hidden;
    flex: 1;
    z-index: 10;
    justify-content: center;
    align-items: center;
  }
  .content1 > div >div> .img5 {
    width: 170px;
  }

  .content1 > div >div> .img6 {
    width: 170px;
  }

  .content1 > div >div> .img7 {
    left: 50%;
    width: 170px;
  }

  .content1 > .img8 {
    z-index: 1;
    top: 360px;
    right: 0;
    width: 130px;
    margin-left: -65px;
  }
  input{
    outline:none;
    background:transparent;
    border:none;
    outline:medium;
    top: 0;
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    z-index: 100;
  }
  .input1 {
    padding-left: 60px;
  }
  .input2{
    padding-left:80px;
  }
  .span1{
    position: absolute;
    z-index: 1000;
    float: right;
    top: 365px;
    right: 40px;
    color: #0BB2D4;
    font-size:10px;
  }

  .reg {
    position: absolute;

    width: 100px;
    height: 0.9rem;
    line-height: 0.9rem;
    font-size: 0.35rem;

    border-radius: 30px;

    color: #fff;
    background: #2079f8;
    float: right;
    top: 395px;
    left: 110px;
  }

  input{
    color: #707274;
  }

  .success {
    width:90%;
    margin: auto;
    text-align: center
  }

  h2{
  font-size: .7rem;
  margin-top: 0.6rem;
  margin-bottom: 0.2rem;
  }

  i {
    display: block;
    padding-top: 30%;
    font-size: 2.8rem;
    color: #52b838;
  }
  p {
    width: 92%;
    font-size: .35rem;
    margin: auto
  }

</style>



