<template>
  <div class="main">
    <h2>爆款产品</h2>
    <div class="main_box">
      <ul>
     <!--   <img src="https://shopstatic.vivo.com.cn/vivoshop/commodity/20180405/20180405101609299910_original.jpg"
             width="100%">-->
        <li class="first" @click="open(data.firstHotGoodsList.id)">
          <div class="first-left">
            <img v-lazy="data.firstHotGoodsList.primaryPicUrl" alt="图片">
          </div>
          <div class="first-right">
            <p class="goods-name">{{data.firstHotGoodsList.name}}</p>
            <p class="goods-nametwo">{{data.firstHotGoodsList.goodsBrief}}</p>
            <p class="goods-price">￥{{data.firstHotGoodsList.retailPrice|toFixed}}</p>
          </div>
        </li>
        <li v-for="(list,index) in data.hotGoodsList" :key="list.id" @click="open(list.id)">
          <div class="list">
            <div class="image">
              <img v-lazy="list.primaryPicUrl" alt="图片">
            </div>
            <p class="name">{{list.name}}</p>
            <p class="nametwo">{{list.goodsBrief}}</p>
            <p class="Price">￥{{list.retailPrice}}</p>
          </div>
        </li>
      </ul>
    </div>

    <h2>本月上新</h2>
    <div class="main_box">
      <ul>
        <li class="first" @click="open(data.firstnewGoods.id)">
          <div class="first-left">
            <img v-lazy="data.firstnewGoods.primaryPicUrl" alt="图片">
          </div>
          <div class="first-right">
            <p class="goods-name">{{data.firstnewGoods.name}}</p>
            <p class="goods-nametwo">{{data.firstnewGoods.goodsBrief}}</p>
            <p class="goods-price">￥{{data.firstnewGoods.retailPrice|toFixed}}</p>
          </div>
        </li>
        <li v-for="(list,index) in data.newGoodsList" :key="list.id" @click="open(list.id)">
          <div class="list">
            <div class="image">
              <img v-lazy="list.primaryPicUrl" alt="图片">
            </div>
            <p class="name">{{list.name}}</p>
            <p class="nametwo">{{list.goodsBrief}}</p>
            <p class="Price">￥{{list.retailPrice}}</p>
          </div>
        </li>
      </ul>
    </div>

  </div>

</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  name: "HomeContainer",
  props: {
    data: {}
  },
  methods: {
    open: function(id) {
      this.$router.push({ path: "goodDetail", query: { id: id } });
    }
  }
};
</script>

<style lang="stylus" scoped>
.main h2 {
  display: block;
  height: 1.3rem;
  background: white;
  font-size: 0.35rem;
  line-height: 1.3rem;
  padding-left: 0.3rem;
}

.main_box {
  /*height: 339px;*/
}

.list {
  height: 4.7rem;
  background: white;
  float: left;
  width: 50%;
  border-right: 1px solid #f4f4f4;
  border-top: 1px solid #f4f4f4;
}

.list span {
  display: block;
  color: red;
  padding-left: 0.5rem;
  padding-top: 0.1rem;
}

.main_box ul {
  overflow: hidden;
  margin-bottom: 0.2rem;
}

ul li {
  list-style: none;
}

.Price {
  font-size: 0.33rem;
  color: red;
  margin: auto;
  text-align: center;
  font-size: 0.3rem;
}

.name {
  width: 80%;
  height: 0.5rem;
  line-height: 0.5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: auto;
  font-size: 0.29rem;
  font-weight: 800;
  text-align: center;
}

.nametwo {
  height: 0.48rem;
  font-size: 0.1rem;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.image {
  width: 100%;
  background: white;

  img {
    width: 2.5rem;
    height: 2.3rem;
    display: block;
    margin: auto;
    margin-top: .5rem;
    margin-bottom .2rem
  }
}

  .first {
    height:4.2rem;
    background-color white;
    .first-left {
      float: left;
      width:50%;
      padding: 10px 10px 10px 40px;
      img {
        width: 3.5rem;

      }
    }
    .first-right{
      padding: 5px 10px 10px 0px;
      width:50%;
      float:left;
      height 4.2rem;
      vertical-align middle;
      text-align center;

      .goods-name{
        font-size 0.4rem;
        padding-top: 0.2rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-weight bold
      }

      .goods-nametwo{
        font-size 0.3rem;
        padding-top: 0.3rem;
       /* overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;*/
      }
      .goods-price{
        padding-top:0.7rem;
        font-size:0.4rem
        color:red;
        text-align center;
      }
    }
  }
</style>



