<template>

  <div class="home">
     <!-- <div class="Homeheader"><img src="/static/img/logo.png"> </div>-->
    <Loading v-if="isLoading"></Loading>
    <div v-else>
      <!--<div style="font-size: 0.4rem;    word-break: break-all;"><p>{{debug}}</p></div>-->
      <Home-Swipe :swiper="banner"></Home-Swipe>
      <Home-List></Home-List>
      <Home-Container :data="result"></Home-Container>

      <Home-Footer></Home-Footer>
    </div>

  </div>

</template>

<script>
import { mapState,mapMutations,mapGetters  } from 'vuex';
import { Swipe, SwipeItem } from 'mint-ui';
import { MessageBox,Toast } from 'mint-ui';
import HomeSwipe from './component/HomeSwipe';
import HomeList from './component/HomeList';
import HomeContainer from './component/HomeContainer'
import HomeFooter from '../../pages/footer'
import axios from 'axios';
import request from '@/utils/request'
import weixi from '@/utils/weixi'


function getQueryString(name) {
  let reg = `(^|&)${name}=([^&]*)(&|$)`
  let r = window.location.href.substr(1).match(reg);
  if (r != null) return unescape(r[2]); return null;
}

export default {

  data(){
    return{
      todos:[],
      banner: [],
      isLoading: true,
      debug: '',
      result: []
    }
  },
  components:{

    HomeSwipe,
    HomeList,
    HomeContainer,
    HomeFooter
  },
  created:function(){
    weixi.checkWx(this.$route);
  },
  mounted: function(){
    this.getData();
  },
  methods:{
    getData:function(){
      var _this=this

      request({
        url: '/wx/home/index',
        method: 'get'
      }).then(function({data}) {
        if (data.errno == 0){
          _this.isLoading = false;
          _this.banner = data.data.banner;
          var res = data.data;

          res.firstHotGoodsList = res.hotGoodsList.shift();
          res.firstnewGoods = res.newGoodsList.shift()

          _this.result = res;
          console.log(res)
        }
      });

/*      axios.get("/static/ceshi.json").then(function(res){
        console.log(res)
        _this.todos=res.data.data.home
      })*/
    }
  }
}
</script>
<style>
  .home{
    border-bottom: 10px;
    padding-bottom: 50px;
  }
  .Homeheader i{
    font-size: 0.55rem;
  }
   .Homeheader{
     position: fixed;
     width: 100%;
     top: 0;
     z-index: 1;
     height: 1.3rem;
     line-height: 1.3rem;
     font-size: 0.35rem;
     padding-left: 0.3rem;
     background: white;
   }
   .swipe{
     height: 6.5rem;
     margin-top: 1px;
   }
   .swipe img{
      width: 100%;
      height: 6.5rem;
  }

    .official{
      width: 100%;
      height: 0.8rem;
      background: white;
      margin-top: 1.33rem;
    }
</style>




