<template>
<div>
<Classify-Header title="商品分类"></Classify-Header>
  <Loading v-if="isLoading"></Loading>
  <div class="calssify-con" v-else>
      <div class="calssify-left" ref="wrapper">
          <ul class="calssify-left-ul" >
              <li v-for="(list,index) in left" :key="index" @click="qiehuan(index)" :class="{active:index===classifyIndex}">
                  {{list.name}}
              </li>
          </ul>
      </div>
      <div class="calssify-rigth" ref="wrapper2">
         <!-- <div style="padding:5px 5px 5px">
            <img :src="left[classifyIndex].wapBannerUrl" style="width:100%;">
          </div>-->
          <ul class="calssify-left-ul">
              <li v-for="(list,index) in right" :key="index" @click="goDetails(list.id)">
                   <img v-lazy="list.primaryPicUrl" :key="list.primaryPicUrl" >
                  <span>{{list.name}}</span>
                  <span class="price">￥{{list.retailPrice}}</span>
              </li>
          </ul>
      </div>
  </div>
<v-footer></v-footer>
</div>

</template>

<script>
import ClassifyHeader from "../../common/header-two";
import footer from "../../pages/footer";
import { mapGetters } from "vuex";
import { Toast, Indicator } from 'mint-ui';
import request from '@/utils/request'

export default {
  data() {
    return {
      left: [],
      rigth: [],
      list: [],
      ce: [],
      key2: "",
      classifyIndex: 0,
      isLoading: true,
    };
  },
  components: {
    "v-footer": footer,
    ClassifyHeader
  },
  //    mounted(){
  //       this.$nextTick(() => {
  //         this.scroll = new BScroll(this.$refs.wrapper, {})
  //         this.scroll = new BScroll(this.$refs.wrapper2, {})
  //       })
  //   },
  computed: {
    ...mapGetters(["this.$store.state.sindex"])
  },
  mounted() {
    var _this = this;

    request({
      url: '/wx/catalog/index',
      method: 'get'
    }).then(function({data}) {
      _this.isLoading = false;
     _this.left = data.data.categoryList;
      _this.list = data.data.right;
      _this.right = _this.list[0];

    });
  },
  methods: {
    qiehuan(index) {
      var _this = this;
      _this.classifyIndex = index;
      _this.right = _this.list[index];
    },
    goDetails(id) {
      console.log(id);
      this.$router.push({
        path: "goodDetail",
        query: { id: id }
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
.active {
    border-left: 2px solid;
    background: #ffffff;
    color: #199cfe;
}

.calssify-con {
    display: flex;
    overflow: hidden;
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    padding-top: 1.45rem;

    .calssify-left {
        flex: 0 0 2.9rem;
        width: 4rem;
        height: 100%;
        background: #f6f6f6;
        // border-right: 10px solid #f6f6f6;
        margin-bottom: 1.51rem;
        font-size 0.35rem

        li {
            height: 1.3rem;
            line-height: 1.3rem;
            text-align: center;
        }
    }

    .calssify-rigth {
        flex: 1;
      /*  height: 100%;*/
        overflow-y: scroll;
        background: white;
        margin-bottom: 1.51rem;
        padding-bottom: 0.1rem;

        ul {
            display: flex;
            flex-wrap: wrap;

            li {
                display: flex;
                flex-direction: column;
                text-align: center;
                width: 33%;
                margin-top: 0.3rem;
                font-size: 0.34rem;
                float: left;

                img {
                    width: 1.98rem;
                    display: block;
                    margin: auto;
                }

                span {
                    /*color: #999;*/
                    /*line-height: 0.9rem;*/
                    font-size: 0.34rem;


                }
              .price {
                font-size: 0.33rem;
                color: red;
                margin: auto;
                text-align: center;
                font-size: 0.3rem;
              }
            }
        }
    }
}



</style>



