export default {
  //验证手机号码部分
  validatePhone: function (phone) {

    var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
    if (phone == '') {
      return false;
    } else if (!reg.test(phone)) {
      return false;
    }
    return true;
  },

}



