<template>
  <div>
    <my-header title="收银台"></my-header>
    <div class="container1">
      <mt-cell title="订单金额：" >
        <span style="color: red">{{payAmount}}</span>
      </mt-cell>
      <!--<p>订单编号：<span>{{orderId}}</span></p>-->
      {{result}}

    </div>
    <div class="pay">
      <mt-button type="primary" @click="pay" size="large">微信支付</mt-button>
      <mt-button type="normal" @click="goOrder" size="large" plain>我的订单</mt-button>
      <mt-button type="normal" @click="goHome" size="large" plain>返回首页</mt-button>
    </div>


  </div>
</template>

<script>
  import { MessageBox,Toast,Button } from 'mint-ui';
  import myHeader from "../../common/header-two";
  import api from '@/api'
    export default {
      name: "weixiPay",
      data(){
        return{
          orderId: this.$route.query.orderId,
          payAmount: '¥ '+ parseFloat(this.$route.query.amount).toFixed(2), //待付金额
          result: '',
        }
      },
      components:{
        myHeader
      },
      created(){
        this.pay();
      },
      methods:{
        pay:function () {
          var _this = this;
          var order = {orderId: this.orderId }
          api.prepay(order).then(function({data}){
            console.log(data)
            if (data.errno ==0){

              var appId = data.data.appId
              var timestamp = data.data.timeStamp
              var nonceStr = data.data.nonceStr
              var packageValue = data.data.packageValue
              var paySign =  data.data.paySign
              var paymentNo = data.data.paymentNo
              //唤起微信支付
              function onBridgeReady(){
                WeixinJSBridge.invoke(
                  'getBrandWCPayRequest', {
                    "appId":appId,     //公众号名称，由商户传入
                    "timeStamp":timestamp,         //时间戳，自1970年以来的秒数
                    "nonceStr":nonceStr, //随机串
                    "package": packageValue,
                    "signType":"MD5",         //微信签名方式：
                    "paySign":paySign //微信签名
                  },
                  function(res){
                    //_this.result = JSON.stringify(res);
                    if(res.err_msg == "get_brand_wcpay_request:ok" ) {
                      //查询支付结果

                        _this.$router.push({path: "OrderDetails",
                          query:{
                            id: _this.orderId
                          }
                        })

                    } // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回 ok，但并不保证它绝对可靠。
                    else if (res.err_msg == "get_brand_wcpay_request:cancel"){
                      Toast("用户取消支付!")
                    }
                  }
                );
              }
              if (typeof WeixinJSBridge == "undefined"){
                if( document.addEventListener ){
                  document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                }else if (document.attachEvent){
                  document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                  document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                }
              }else{
                onBridgeReady();
              }
              return false;

            }
          })
        },
        goOrder:function () {
          this.$router.push({
            path:'/order'
          })
        },
        goHome:function () {
          this.$router.push({
            path:'/home'
          })
        }
      }
    }
</script>

<style lang="stylus" scoped>
  .container1 {
    /*padding-top: 0.2rem;*/
    width: 100%;
    font-size 0.3rem;
  }
  .pay {
    padding:0.2rem 0.2rem ;
  }
</style>



