<template>
  <div class="order" id="transition">
      <Order-Header title="我的订单"></Order-Header>
        <div class="orderMain">
            <div  class="order-box">
                <p  v-for="(c,index) in t"
                @click="changeStatus(index)"
                >
                    <a :class="{active:index===nowIndex}">{{c.item}}</a>
                </p>
            </div>
            <div class="order-box2" >
               <div >
                     <div v-for="(list,index) in orders" :key="index" class="orders">

                       <div class="_order">
                            <div class="left">
                                <i class="iconfont icon-qijiandian"></i>
                                订单编号：{{list.orderSn}}
                            </div>
                            <div class="right">{{list.orderStatusText}}</div>
                        </div>
                       <div style="clear: both"></div>
                        <div class="order" v-for="goods in list.goodsList">
                           <div>
                            <div class="goods">
                              <img :src="goods.picUrl"></img>
                            </div>
                            <div class="order-div">
                                <h3>{{goods.goodsName}}</h3>
                                <!-- <p class="order-div-color">颜色:黑</p> -->
                                <p class="order-div-price">￥{{goods.price}}</p>
                            </div>
                            <div class="order-div-2">
                                × {{goods.number}}
                            </div>
                        </div>
                        </div>
                        <div class="order-2">
                            <div class="order-2-box">
                                <p class="order-2-zero">共计<span>{{list.count}}</span>件商品</p>
                                <p class="order-2-one">总计：<span>￥{{list.actualPrice}}</span></p>
                                <p class="order-2-two">(积分抵扣：¥{{list.integralPrice|toFixed}} 含运费：¥{{list.freight|toFixed}} 优惠：¥0.00)</p>
                            </div>
                        </div>
                        <div class="order-3" >
                          <a class="pay" v-show="list.handleOption.pay" @click.stop="onPay(list.id,list.actualPrice)">去支付</a>
                          <!--<a @click.stop="odefault(index)">订单删除</a>-->
                          <a @click.stop="onCancel(list.id)" v-show="list.handleOption.cancel">取消订单</a>
                          <a @click.stop="onConfirm(list.id)" v-show="list.handleOption.confirm">确认收货</a>
                          <a @click.stop="odetails(list)">查看详情</a>
                        </div>
                    </div>
               </div>

            </div>
        </div>
  </div>
</template>

<script>
import OrderHeader from "@/common/header";
import { Toast } from 'vant';
import {MessageBox} from "mint-ui";
import api from '@/api'
import weixi from '@/utils/weixi';
export default {
  name: "order",
  data() {
    return {
      nowIndex: 0,
      t: [
        {
          item: "全部",
        },
        {
          item: "待付款"
        },
        {
          item: "待发货"
        },
        {
          item: "待收货"
        },
    /*    {
          item: "待评价"
        }*/
      ],
      orders:null,
    };
  },
  components: {
    OrderHeader
  },
  created: function () {
    this.orderList(this.nowIndex);
  },
  methods: {
    changeStatus(num) {
      this.nowIndex = num;
      this.orderList(num);
    },
    odetails(list){
        this.$router.push({
            path: "OrderDetails",
            query:{
                id:list.id
            }
        })
        //console.log(list)
    },
    orderList(status){
      Toast.loading();
      var _this = this;
      var params = {
        showType: this.nowIndex
      }
      api.orderList(params).then(function ({data}) {
        if (data.errno ==0){
          _this.orders = data.data.data;
          //console.log(_this.orders)
        }
        else if(data.errno == 401){
          _this.$router.push({
            path:"/login"
          })
        }
        Toast.clear();
      })
    },
    /**
     * 取消订单
     * @param list
     */
    onCancel(id){
      var _this = this;
      MessageBox.confirm("确认取消这笔订单?").then(action=>{
        if (action == 'confirm'){
          Toast.loading();
          var data = {orderId: id}
          api.cancelOrder(data).then(function ({data}) {
            if (data.errno == 0){
              _this.orderList(_this.nowIndex);
            }
            else{
              Toast.clear();
            }

          })
        }
      })

    },
    /**
     * 确认订单
     */
    onConfirm(id){
      var params = {orderId: id}
      var _this = this;

      MessageBox.confirm("确认货品已经收到?").then(action =>{
        if (action == "confirm"){
          Toast.loading();
          api.confirmOrder(params).then(({data})=>{
            if(data.errno ==0){

              _this.orderList(_this.nowIndex);
            }
            Toast.clear();
          })
        }
      });
    },
    onPay(id,amount){
      this.$router.push({
        path: '/weixiPay',
        query: { orderId: id,amount: amount}
      })
    }
  }
};
</script>


<style lang="stylus" scoped>
.orderMain
    padding-top 1.3rem
.order-box
    height 1rem
    background white
    position fixed
    width 100%
    border-bottom 1px solid #f0f0f0
    .active
        color #199cfe
        border-bottom 2px solid #199cfe
    p
        width 25%
        height 100%
        line-height 1rem
        float left
        text-align center

        a
            width 50%
            height 100%
            font-size .36rem
            display block
            margin auto
.order-box2
    width 100%
    margin-top 1rem
.orders
    margin-top 10px
    ._order
        width 100%
        height 1.3rem
        line-height 1.3rem
        background #ffffff
        .left
            float left
            color #666
            margin-left .5rem
            font-size .35rem
            i
                font-size .4rem
                color #666
        .right
            float right
            color rgb(0, 172, 255)
            margin-right .3rem
            font-size .35rem
    .order
        height 3rem
        border-bottom 1px solid #f0f0f0
        border-top 1px solid #f0f0f0
        .goods
            width: 90px;
            height 90px;
            float left
            margin .3rem
            img
              height 90px
              width 90px
              float left
              display block
        .order-div
            width:5.5rem
            margin .2rem
            line-height .7rem
            float left
            h3
                font-size .35rem
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            .order-div-price
                color red
                font-size .4rem
        .order-div-2
            display inline-block
            float right
            margin .2rem .2rem
            font-size .4rem

    .order-2
        width 100%
        height 1.7rem
        background white
        .order-2-box
            /*width 50%*/
            float right
            text-align right
            margin .2rem .4rem
            .order-2-zero
                float left
                font-size .35rem
                margin-left .5rem
                span
                    color red
            .order-2-one
                font-size .35rem
                span
                    color red
            .order-2-two
                color #666
                padding-top .2rem
                font-size .35rem
    .order-3
        width 100%
        height 1.1rem
        background white
        a
            padding:5px 10px 5px;
            height .7rem
            /*line-height .7rem*/
            border 1px solid red
            border-radius 3px
            text-align center
            display block
            float right
            margin-right .3rem
            margin-top .1rem
            border 1px solid #dcdcdc
            font-size .3rem
        .pay
          background-color #199cfe
          color white
          border: 1px solid #199cfe;

</style>




